import React from "react"
import Layout from "../components/layout"
import { graphql } from "gatsby"
import { Link } from "gatsby"

function latLonToMetres(lat1, lon1, lat2, lon2) {
  if (lat1 === null || lat2 === null || lon1 === null || lon2 === null) {
    return "Location not available"
  }
  const R = 6371000 // Radius of the earth in m
  const dLat = (lat2 - lat1) * (Math.PI / 180)
  const dLon = (lon2 - lon1) * (Math.PI / 180)
  const a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(lat1 * (Math.PI / 180)) *
      Math.cos(lat2 * (Math.PI / 180)) *
      Math.sin(dLon / 2) *
      Math.sin(dLon / 2)
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a))
  const d = R * c // Distance in m
  return Math.round(d)
}

let isLocationAvailable = true

export default class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      toilets: this.props.data.allToilets.nodes.slice(0),
      myLat: null,
      myLon: null,
      sortBy: `name`,
    }

    this.sortByDistance = this.sortByDistance.bind(this)
    this.compareDistance = this.compareDistance.bind(this)
    this.sortByName = this.sortByName.bind(this)
    this.updatePosition = this.updatePosition.bind(this)
    this.handleChange = this.handleChange.bind(this)
  }

  componentDidMount() {
    this.updatePosition()
  }

  handleChange(event) {
    this.setState({
      sortBy: event.target.value,
    })

    if (event.target.value === "name") {
      this.sortByName()
    } else {
      this.sortByDistance()
    }
  }

  sortByDistance() {
    let toilets = this.state.toilets.slice(0)
    toilets.sort(this.compareDistance)

    this.setState({
      toilets: toilets,
    })
  }

  compareDistance(t1, t2) {
    const t1ToMe = latLonToMetres(
      t1.lat,
      t1.lon,
      this.state.myLat,
      this.state.myLon
    )
    const t2ToMe = latLonToMetres(
      t2.lat,
      t2.lon,
      this.state.myLat,
      this.state.myLon
    )
    return t1ToMe - t2ToMe
  }

  sortByName() {
    let toilets = this.state.toilets.slice(0)
    toilets.sort((t1, t2) => t1.name.localeCompare(t2.name))

    this.setState({
      toilets: toilets,
    })
  }

  updatePosition() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        pos => {
          this.setState({
            myLat: pos.coords.latitude,
            myLon: pos.coords.longitude,
          })
        },
        () => {
          isLocationAvailable = false
          this.forceUpdate()
        },
        { enableHighAccuracy: true }
      )
    } else {
      // Browser doesn't support Geolocation
      isLocationAvailable = false
      this.forceUpdate()
    }
  }

  render() {
    // console.log(this.state)
    return (
      <Layout main={true}>
        <label>Sort by: </label>
        <select value={this.state.sortBy} onChange={this.handleChange}>
          <option value="name">Name</option>
          {isLocationAvailable && <option value="distance">Distance</option>}
        </select>
        {!isLocationAvailable && (
          <p>
            Location services not working! (Add help popup, convert to
            component)
          </p>
        )}
        <table style={{ tableLayout: "fixed" }}>
          <thead>
            <tr>
              <th>Name</th>
              <th>Distance (m)</th>
            </tr>
          </thead>
          <tbody>
            {this.state.toilets.map((toilet, index) => (
              <tr key={index}>
                <td>
                  <Link to={"/" + toilet.name.replace(/\s/g, "")}>
                    {toilet.name}
                  </Link>
                </td>
                <td>
                  {latLonToMetres(
                    this.state.myLat,
                    this.state.myLon,
                    toilet.lat,
                    toilet.lon
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </Layout>
    )
  }
}

export const query = graphql`
  query {
  allToilets(sort: {fields: name}) {
    nodes {
      name
      facilities {
        hose
        showerHeads
      }
      paranoma {
        femaleYaw
        maleYaw
        startingYaw
        url
        handicappedYaw
      }
      lat
      lon
    }
  }
}
`
